@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{overflow:hidden}*{font-family:'Noto Sans KR', sans-serif;box-sizing:border-box}.wrapper{padding-top:300px;margin:0 auto;width:500px}.wrapper .content-wrapper .result-wrapper{text-align:center;font-size:25px;font-weight:900}.wrapper .content-wrapper .result-wrapper .result-title{margin-bottom:15px}.wrapper .content-wrapper .result-wrapper .result-content{padding:30px 0;font-size:30px;border:3px solid black}.wrapper .content-wrapper .lotto-wrapper{margin-top:50px;text-align:center}.wrapper .content-wrapper .lotto-wrapper button{overflow:hidden;position:relative;padding:10px 20px;font-size:15px;font-weight:900;outline:0;border:2px solid black;border-radius:10px;background-color:transparent}.wrapper .content-wrapper .lotto-wrapper button:before{z-index:-1;content:'';display:block;position:absolute;top:0;left:-102%;width:100%;height:100%;background:lightgray;transition:left 0.6s ease}.wrapper .content-wrapper .lotto-wrapper button:hover{cursor:pointer}.wrapper .content-wrapper .lotto-wrapper button:hover:before{left:0}

